//         

let serverTimeOffset = 0; //        

//    
export const getServerTime = () => {
  return new Date(Date.now() + serverTimeOffset);
};

//    
export const setServerTimeOffset = (offset) => {
  serverTimeOffset = offset;
};

//    
export const syncWithServer = async () => {
  try {
    const startTime = Date.now();
    const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';
    const response = await fetch(`${apiUrl}/time`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      timeout: 5000, // 5  
    });
    
    if (response.ok) {
      const data = await response.json();
      const endTime = Date.now();
      const networkDelay = (endTime - startTime) / 2; //   
      
      const serverTime = new Date(data.serverTime).getTime();
      const clientTime = startTime + networkDelay;
      
      serverTimeOffset = serverTime - clientTime;
      
      console.log('   . :', serverTimeOffset, '');
      return true;
    }
  } catch (error) {
    console.error('  :', error);
  }
  return false;
};

//    
export const formatLastSeen = (lastSeen) => {
  if (!lastSeen) return '    ';
  
  const now = getServerTime();
  const lastSeenDate = new Date(lastSeen);
  const diffInMinutes = Math.floor((now - lastSeenDate) / (1000 * 60));
  const diffInHours = Math.floor(diffInMinutes / 60);
  const diffInDays = Math.floor(diffInHours / 24);
  
  if (diffInMinutes < 1) {
    return ' ';
  } else if (diffInMinutes < 60) {
    return `${diffInMinutes} . `;
  } else if (diffInHours < 24) {
    return `${diffInHours} . `;
  } else if (diffInDays < 7) {
    return `${diffInDays} . `;
  } else {
    return lastSeenDate.toLocaleDateString('ru-RU', {
      day: 'numeric',
      month: 'short',
      year: lastSeenDate.getFullYear() !== now.getFullYear() ? 'numeric' : undefined
    });
  }
};

//    ( )
export const formatMessageTime = (date) => {
  return new Date(date).toLocaleTimeString('ru-RU', { 
    hour: '2-digit', 
    minute: '2-digit' 
  });
};

//     
export const formatChatTime = (date) => {
  const messageDate = new Date(date);
  const now = getServerTime();
  const diffInDays = Math.floor((now - messageDate) / (1000 * 60 * 60 * 24));
  
  if (diffInDays === 0) {
    //  -   
    return messageDate.toLocaleTimeString('ru-RU', { 
      hour: '2-digit', 
      minute: '2-digit' 
    });
  } else if (diffInDays === 1) {
    // 
    return '';
  } else if (diffInDays < 7) {
    //    -   
    return messageDate.toLocaleDateString('ru-RU', { weekday: 'short' });
  } else {
    //   -  
    return messageDate.toLocaleDateString('ru-RU', { 
      day: 'numeric', 
      month: 'short' 
    });
  }
};
