import React, { useRef, useEffect } from 'react';
import { FiUser, FiArchive, FiTrash2 } from 'react-icons/fi';
import styles from './ChatMenu.module.css';

const ChatMenu = ({ chat, user, onClose, isOpen, onLogout, onOpenProfile }) => {
  const menuRef = useRef(null);

  //      
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        onClose();
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen, onClose]);

  const handleMenuItemClick = (action) => {
    onClose();
    
    switch (action) {
      case 'profile':
        //   
        onOpenProfile();
        break;
      case 'archive':
        //  
        console.log(' ');
        break;
      case 'delete':
        //  
        console.log(' ');
        break;
      default:
        break;
    }
  };

  if (!isOpen) return null;

  return (
    <div ref={menuRef} className={styles.menuDropdown}>
      <div className={styles.menuHeader}>
        <h3 className={styles.menuTitle}> </h3>
      </div>

      <div className={styles.menuItems}>
        {/*   */}
        <button
          onClick={() => handleMenuItemClick('profile')}
          className={styles.menuItem}
        >
          <FiUser size={18} />
          <span> </span>
        </button>

        {/*  */}
        <div className={styles.menuDivider} />

        {/*   */}
        <button
          onClick={() => handleMenuItemClick('archive')}
          className={styles.menuItem}
        >
          <FiArchive size={18} />
          <span></span>
        </button>

        {/*   */}
        <button
          onClick={() => handleMenuItemClick('delete')}
          className={`${styles.menuItem} ${styles.dangerItem}`}
        >
          <FiTrash2 size={18} />
          <span> </span>
        </button>
      </div>
    </div>
  );
};

export default ChatMenu;
