import React, { useState, useRef, useEffect } from 'react';
import { FiTrash2, FiMoreHorizontal, FiArchive, FiBellOff } from 'react-icons/fi';
import styles from './SwipeableChatItem.module.css';

const SwipeableChatItem = ({ 
  children, 
  onDelete, 
  onArchive, 
  onMute, 
  onMore,
  isMobile = false 
}) => {
  const [isSwiped, setIsSwiped] = useState(false);
  const [startX, setStartX] = useState(0);
  const [currentX, setCurrentX] = useState(0);
  const [isDragging, setIsDragging] = useState(false);
  const containerRef = useRef(null);
  const SWIPE_THRESHOLD = 80; //    

  // Touch    preventDefault
  const handleTouchStart = (e) => {
    if (!isMobile) return;
    setStartX(e.touches[0].clientX);
    setIsDragging(true);
  };

  const handleTouchMove = (e) => {
    if (!isMobile || !isDragging) return;
    const deltaX = e.touches[0].clientX - startX; //  
    setCurrentX(deltaX);
    
    //     ( )
    if (deltaX < 0) {
      setIsSwiped(Math.abs(deltaX) > SWIPE_THRESHOLD);
    }
  };

  const handleTouchEnd = () => {
    if (!isMobile || !isDragging) return;
    setIsDragging(false);
    
    if (Math.abs(currentX) > SWIPE_THRESHOLD) {
      setIsSwiped(true);
    } else {
      setIsSwiped(false);
      setCurrentX(0);
    }
  };

  //     -   
  const handleMouseDown = (e) => {
    //     
    return;
  };

  const handleMouseMove = (e) => {
    //     
    return;
  };

  const handleMouseUp = () => {
    //     
    return;
  };

  const handleRightClick = (e) => {
    e.preventDefault();
    if (onMore) {
      onMore(e);
    }
  };

  const resetSwipe = () => {
    setIsSwiped(false);
    setCurrentX(0);
  };

  //   
  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    if (isMobile) {
      // Touch   passive: false  preventDefault
      const touchMoveHandler = (e) => {
        if (!isDragging) return;
        e.preventDefault(); //   
        const deltaX = e.touches[0].clientX - startX; //  
        setCurrentX(deltaX);
        
        if (deltaX < 0) {
          setIsSwiped(Math.abs(deltaX) > SWIPE_THRESHOLD);
        }
      };

      container.addEventListener('touchstart', handleTouchStart, { passive: true });
      container.addEventListener('touchmove', touchMoveHandler, { passive: false });
      container.addEventListener('touchend', handleTouchEnd, { passive: true });
      
      return () => {
        container.removeEventListener('touchstart', handleTouchStart);
        container.removeEventListener('touchmove', touchMoveHandler);
        container.removeEventListener('touchend', handleTouchEnd);
      };
    } else {
      //     ,  
      container.addEventListener('contextmenu', handleRightClick);
      
      return () => {
        container.removeEventListener('contextmenu', handleRightClick);
      };
    }
  }, [isMobile, isDragging, currentX, startX]);

  return (
    <div className={styles.swipeableContainer}>
      {/*    -    */}
      {isMobile && (
        <div className={`${styles.swipeActions} ${isSwiped ? styles.swipeActionsVisible : ''}`}>
          <button 
            className={`${styles.actionButton} ${styles.deleteButton}`}
            onClick={onDelete}
            title=" "
          >
            <FiTrash2 size={18} />
          </button>
          <button 
            className={`${styles.actionButton} ${styles.archiveButton}`}
            onClick={onArchive}
            title=""
          >
            <FiArchive size={18} />
          </button>
          <button 
            className={`${styles.actionButton} ${styles.muteButton}`}
            onClick={onMute}
            title=" "
          >
            <FiBellOff size={18} />
          </button>
          <button 
            className={`${styles.actionButton} ${styles.moreButton}`}
            onClick={onMore}
            title=""
          >
            <FiMoreHorizontal size={18} />
          </button>
        </div>
      )}

      {/*   */}
      <div
        ref={containerRef}
        className={`${styles.swipeableContent} ${isSwiped ? styles.swiped : ''}`}
        style={{
          transform: `translateX(${isSwiped ? -200 : Math.min(0, currentX)}px)`,
          transition: isDragging ? 'none' : 'transform 0.3s ease'
        }}
        onClick={resetSwipe}
      >
        {children}
      </div>
    </div>
  );
};

export default SwipeableChatItem;
